<template>
	<div>
		<div class="search-address" id="search_address">
			<div class="header">
				<router-link v-bind:to="'/'">
					<i class="iconfont back icon-back"></i>
				</router-link>
				<h1>选择地址</h1>
			</div>
			<div class="search-bar">
				<input type="text" name="search" id="search" 
				v-on:keyup.13="search"
				v-model.trim="keyword" 
				placeholder="请输入地址"/>
			</div>

			<div class="list-box">
				<ul>
					<li v-for="item in addresslist">
						<h3>{{ item.short_address }}</h3>
						<p>{{ item.address }}</p>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	name: 'search_address',
	data: function() {
		return {
			keyword: "",
			url: "https://mainsite-restapi.ele.me/bgs/poi/search_poi_nearby",
			addresslist: []
		}
	},
	methods: {
		search: function() {
			if(!this.keyword) return;
			this.$http.get(this.url,{
				params: {
					keyword: this.keyword
				}
			}).then(function(response) {
				// this.keyword = "";
				// console.log(response);
				this.addresslist = response.body;
			})
		}
	}
}
</script>
<style scoped>
	.search-address {
		width: 100%;
		display: block;
		background-color: #fff;
	}
	.search-address .header h1 {
		display: inline-block;
	}
	.search-address .header {
		max-width: 720px;
		width: 100%;
		/*top:0;*/
		height: 45px;
		/*position:fixed;*/
		background-color: #0095fc;
		color: #fff;
		padding:.3rem 1rem;
		box-sizing: border-box;
		font-size:1.6rem;
		z-index: 20;
		text-align: center;
		position: relative;
	}
	.search-address .header a {
		color: #fff;
	}
	.search-address .header i {
		font-size: 3.2rem;
		float: left;
		position: absolute;
		left:1rem;
		top:.1rem;
		color: #fff;
	}
	.search-address .header h1 {
		display: inline-block;
		font-size: 1.8rem;
		line-height: 3.5rem;
	}
	.search-address .search-bar {
		width: 100%;
		text-align: center;
		background-color: #0095fc;
		padding:0 1rem 1rem 1rem; 
	}
	.search-address .search-bar input {
		padding:.2rem;
		border: none;
		outline: none;
		height: 28px;
		width: 98%;
		border-radius: 2rem;
		text-align: center;
	}
	.search-address .list-box li{
		padding:.8rem 1.5rem;
		border-bottom: 1px solid #e8e8e8;
	}
	.search-address .list-box li:hover{
		background-color: #daf9ff;
	}
	.search-address .list-box li h3 {
		font-size: 1.6rem;
		color: #333;
	}
</style>


