<template>
	<div>
		<commin-header-login-password></commin-header-login-password>
		<div class="input-info">
			<input type="tel" placeholder="手机/用户名/邮箱" class="phone">
			<div class="password">
				<i-switch size="small"></i-switch>
				<input type="text" placeholder="密码">
			</div>
		</div>

		<div class="attention">
			<div class="some">
				<button class="submit">登录</button>
				<h3>关于我们</h3>
			</div>
		</div>
	</div>
</template>
<script>
import CommonHeaderLoginPassword 	from './CommonHeaderLoginPassword'
export default {
	name: 'password-login',
	components: {
  		'commin-header-login-password' : CommonHeaderLoginPassword
  	}
}
</script>
<style scoped>
.input-info {
	margin-top:55px;
}
.input-info input{
	width: 100%;
	outline: none;
	border:none;
	padding:1rem;
	border-bottom:1px solid #ddd;
	font-size: 1.6rem;
}
.input-info >input {
	border-top:1px solid #ddd;
}
.password {
	position: relative;
}
.password span {
	position: absolute;
	right: 1rem;
	top:1.3rem;
}
.attention {
	margin-top: 1rem;
	padding:0 1rem;
}
.some {
	text-align: center;
	margin-top: .5rem;
	padding:0 1rem;
}
.some button {
	width: 100%;
	background-color: #65e265;
	color:#fff;
	border-radius: .3rem;
	outline: none;
	border:none;
	padding:.7rem 0;
}
.some h3 {
	margin-top: 1rem;
	color: grey;
	font-size: .8rem;
}
</style>


