<template>
	<div>
		 <header class="headerNav">
			<div class="headerTop">
				<router-link v-bind:to="'/searchaddress'">
					<i class="iconfont icon-msnui-pos"></i>
					<input class="location ellipsis" id="location" value="广东省广州市" v-model="locationData">
						<span class="shadow"></span>
					</input>
				<i class="iconfont icon-xiajiantoushixinxiao"></i>
				</router-link>
				<i class="iconfont icon-tianqi weather"></i>
			</div>
			<div class="search-bar">
				<input type="text" name="search" id="search" placeholder="搜索商家,  商品名称"/>
			</div>
			<div class="menu">
				<ul class="menu-list">
					<li><a href="###">肠粉</a></li>
					<li><a href="">寿司</a></li>
					<li><a href="">酸辣粉</a></li>
					<li><a href="">饺子馆</a></li>
					<li><a href="">我的菜</a></li>
				</ul>
			</div>
		</header>
	</div>
</template>
<script>
import SearchAddress from './SearchAddress'
export default {
	name: 'header-search',
	components: {
		'search-address' : SearchAddress
	},
	data: function() {
		return {
			locationData:''
		}
	},
	methods: {
		getLocation: function() {
			//ajax获取用户所在经纬度
			this.$http.jsonp("http://api.map.baidu.com/location/ip?ak=UraOi1ggzItUAG5KTQe1ih51VGoTjkqm&coor=bd09ll")
					  .then(function(response) {
					  	console.log(response)
					  	this.locationData = response.body.content.address;
					  })
		}
	},
	created: function() {
		this.getLocation();
	}
}
</script>
<style scoped>
.headerNav {
	position:fixed;
	max-width: 720px;
	width: 100%;
	top:0;
	padding:1rem 1rem .5rem 1rem;
	box-sizing: border-box;
	background-color: #0095fc;
	z-index: 10;
}
.headerNav a {
	font-size:1.7rem;
	font-weight: bold;
	color: #fff;
	display: inline-block;
	line-height: 2rem;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}
.headerNav .location {
	border:none;
	outline:none;
	display: inline-block;
	height: 100%;
	width: 40%;
	vertical-align: middle;
	position:relative;
	font-size: 2rem;
	color: #fff;
	background-color: transparent;
}
.headerNav .location .shadow {
	position:absolute;
	top:0;
	right:0;
	display: block;
	width: 30%;
	height: 100%;
	background-image:linear-gradient(to right, rgba(255,255,255,0) 20%, hsl(205, 100%, 50%));
}
.headerNav i {
	vertical-align: middle;
}
.headerNav .weather {
	float: right;
	line-height: 2rem;
}
.headerNav .search-bar {
	width: 100%;
	text-align: center;
}
.headerNav .search-bar input {
	padding:.2rem;
	border: none;
	outline: none;
	height: 28px;
	margin-top:1rem;
	width: 98%;
	border-radius: 2rem;
	text-align: center;
}
.menu {
	margin-top:1rem;
}
.menu-list {
	color: #fff;;
	display: flex;
	justify-swiper_1: space-around;
}
.menu-list li {
	flex-basis: 20%;
	text-align: center;
}
.menu-list li a {
	font-size: 1.2rem;
	font-weight: 100;
	-webkit-user-select: none;
}
</style>


