<template>
	<div>
		<div class="shop-car" v-on:click="goTop">
			<Icon type="arrow-up-a" size="20" color="#0095fc"></Icon>
		</div>
	</div>
</template>
<script>
export default {
	name: 'shop-car',
	methods: {
      goTop () {
        window.scrollTo(0, 0)
      }
    }
}
</script>

<style scoped>
.shop-car {
	position:fixed;
	right:1.5rem;
	bottom:70px;
	width:4rem;
	height: 4rem;
	border-radius: 50%;
	background-color: #fff;
	text-align: center;
	box-shadow: 0 2px 3px gray;
}
.ivu-icon {
	font-weight: 800;
	line-height: 2.5;
}
</style>


