<template>
	<div>
		<li class="shop-detalis" v-for="item in infolist">
			<router-link v-bind:to="'/shop'" class="shop-detalis-box">
				<div class="left">
					<img :src="item.image_path"/>
				</div>
				<div class="right">
					<div class="top">
						<div class="title">
							<span class="little-brand">品牌</span>
							<h2>{{ item.name }}</h2>
							<span class="ticket">票</span>
						</div>
						<div class="star-slider">
							<span class="iconfont icon-xingxing"></span>
							<span class="iconfont icon-xingxing"></span>
							<span class="iconfont icon-xingxing"></span>
							<span class="iconfont icon-xingxing"></span>
							<span class="score">{{ item.rating }}</span>
							<span class="sum">月售{{ item.recent_order_num }}单</span>
						</div>
						<div class="delivery-time">
							<span class="delivery-time-details">￥{{item.float_minimum_order_amount }}起送 <i>|</i>配送费￥{{ item.float_delivery_fee }} <i>|</i>{{ item.average_cost }}</span>
							<p class="delivery-time-right">{{ item.distance }}m<i>|</i><span class="delivery-time-right-blue">{{ item.order_lead_time }}分钟</span></p>
						</div>
					</div>
					<div class="bottom">
						<p> <span class="minus">减</span>{{ item.piecewise_agent_fee.description }}</p>
						<p> <span class="new">新</span>{{ item.activities[0].description }}</p>
					</div>
				</div>
			</router-link>
		</li>
	</div>
</template>
<script>
export default {
	name: 'shop-box',
	data: function() {
		return {
			infolist: []
		}
	},
	methods: {
		getData: function() {
			this.$http.get("../../static/json/shoplist.json")
					  .then(function(response) {
					  	// console.log(response);
					  	this.infolist = response.body;
					  })
		}
	},
	created: function() {
		this.getData();
	}
}
</script>

<style scoped>
	.shop-box  .shop-detalis {
		padding-top:1rem;
		padding-bottom: 1rem;
		border-bottom: 1px solid #f1eeee;
		padding-left:.8rem;
		padding-right: .8rem;
	}
	.shop-detalis-box {
		display: flex;
		justify-content: space-between;
	}
	.shop-box  .shop-detalis .left {
		flex-basis: 20%;
		max-width: 100px;
	}
	.shop-box  .shop-detalis .left img{
		/*width: 20vw;*/
	}
	.shop-box  .shop-detalis .right {
		flex-basis: 70%;
	}
	.little-brand {
		display:inline-block;
		width: auto;
		background-color: #FFEB3B;
		border-radius: 3px;
		padding:.1rem .3rem;
		font-size: 8px;
		margin-right: .5rem;
		vertical-align: middle;
	}
	.ticket {
		display: inline-block;
		padding:0 .1rem;
		border:1px solid darkgrey;
		border-radius: 4px 4px;
		color:darkgrey;
		float: right;
		vertical-align: middle;
	}
	.score {
		color:orangered;
		margin-left:.2rem;
	}
	.sum {
		color:grey;
		font-size:1rem;
	}
	.minus {
		display: inline-block;
		padding:0 .15rem;
		height: 1.4rem;
		background-color: #d23d3d;
		border-radius: 4px 4px;
		color:#fff;
		vertical-align: middle;
		margin-right: .5rem;
	}
	.new {
		display: inline-block;
		padding:0 .15rem;
		height: 1.4rem;
		background-color: #49c549;
		border-radius: 4px 4px;
		color:#fff;
		vertical-align: middle;
		margin-right: .5rem;
	}
	.shop-box  .shop-detalis .right .top {
		border-bottom: 1px dashed darkgrey;
	}
	.shop-box  .shop-detalis .right .top .title h2 {
		display: inline-block;
		font-size: 1.3rem;
		font-weight: bold;
		vertical-align: middle;
		color: black;
	}


	.delivery-time {
		margin-bottom: .3rem;
	}
	.delivery-time .delivery-time-details {
		color:grey;
	}
	.delivery-time  i {
		color: rgba(169, 169, 169, 0.44);
	}
	.delivery-time-right {
		float: right;
		color: grey;
	}
	.delivery-time-right-blue {
		color: #0095fc;
	}
	.shop-box  .shop-detalis .right .bottom p {
		color:gray;
		margin-top:.3rem;
	}
</style>


