<template>
<div class="limit-show">
	<div class="top">
		<h2>限时抢购</h2>
		<span class="timer-info">距离结束</span>
		<span class="timer">
			<div id="tiles">
				<span id="tiles1"></span><i>:</i>
				<span id="tiles2"></span><i>:</i>
				<span id="tiles3"></span>
			</div>
		</span>
		<a href="###"><span class="right-more">更多<i class="iconfont more icon-more"></i></span></a>
	</div>
	<div class="limit-list">
		<ul class="sale-list">
			<li>
				<a href="###"> 
					<figure>
						<img src="../assets/img/sale_1.jpeg"/> 
						<figcaption>
							<span class="title ellipsis">大师兄烤肉</span>
							<span class="price">￥9.9 <i class="del">￥29</i></span>
						</figcaption>
					</figure>
				</a>
			</li>
			<li>
				<a href="###"> 
					<figure>
						<img src="../assets/img/sale_2.jpeg"/> 
						<figcaption>
							<span class="title">大师兄烤肉</span>
							<span class="price">￥9.9<i class="del">￥29</i></span>
						</figcaption>
					</figure>
				</a>
			</li>
			<li>
				<a href="###"> 
					<figure>
						<img src="../assets/img/sale_3.jpeg"/> 
						<figcaption>
							<span class="title">大师兄烤肉</span>
							<span class="price">￥9.9<i class="del">￥29</i></span>
						</figcaption>
					</figure>
				</a>
			</li>
			<li>
				<a href="###"> 
					<figure>
						<img src="../assets/img/sale_4.jpeg"/> 
						<figcaption>
							<span class="title">大师兄烤肉</span>
							<span class="price">￥9.9<i class="del">￥29</i></span>
						</figcaption>
					</figure>
				</a>
			</li>
			<li>
				<a href="###"> 
					<figure>
						<img src="../assets/img/sale_4.jpeg"/> 
						<figcaption>
							<span class="title">大师兄烤肉</span>
							<span class="price">￥9.9<i class="del">￥29</i></span>
						</figcaption>
					</figure>
				</a>
			</li>
			<li>
				<a href="###"> 
					<figure>
						<img src="../assets/img/sale_4.jpeg"/> 
						<figcaption>
							<span class="title">大师兄烤肉</span>
							<span class="price">￥9.9<i class="del">￥29</i></span>
						</figcaption>
					</figure>
				</a>
			</li>
		</ul>
	</div>
</div>
</template>
<script>
export default {
	name: 'limit-show',
	mounted(){
	var lateData = new Date('2017-6-1-0:0:0');
	var lateTime = lateData.getTime();
	var tiles1 = document.getElementById('tiles1');
	var tiles2 = document.getElementById('tiles2');
	var tiles3 = document.getElementById('tiles3');
		function clock() {
			var nowData = new Date();
			var nowTime = nowData.getTime();
			var minus = (lateTime - nowTime)/1000; //秒数
			var hours = parseInt(minus/(60*60)%24);
			var minutes = parseInt((minus/60)%60);
			var seconds = parseInt(minus%60);

			tiles1.innerHTML = hours;
			tiles2.innerHTML = minutes;
			tiles3.innerHTML = seconds;
		}
		clock();
		setInterval(clock,1000);
	  }
}
</script>

<style scoped>
.limit-show {
	padding-left:.8rem;
	padding-right: .8rem;
	padding-top:.5rem;
	padding-bottom:.5rem;
	background-color: #fff;
}
.limit-show  a {
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}
.limit-show .top {
	margin-bottom: .5rem;
}
.limit-show .top h2{
	font-size: 1.5rem;
	font-weight: bolder;
	display: inline-block;
	vertical-align: middle;
}
.limit-show .top .timer-info{
	display: inline-block;
	color: #9b9b9b;
	margin-left:1rem;
}
.limit-show .top .timer {
	display: inline-block;
}
.limit-show .top #tiles {
	margin-left: 10px;
	position: relative;
	text-align: center;
}
#tiles i {
	font-size: 1rem;
	font-weight: bolder;
	padding:.2rem;
}
#tiles  span{
	width: 2rem;
	height: auto;
	display: inline-block;
	background-color:black;
	color: #fff;
	margin-right:0;
	text-align: center;
	font-size: 1rem;
	font-weight: bolder;
	box-sizing:border-box;
	border-radius: 3px;
}
.right-more {
	float: right;
	font-size: 1.2rem;
	color:#666666;
	line-height: 2.8rem;
}
.right-more i {
	font-size: 1.5rem;
	vertical-align: middle;
}

.sale-list {
	min-height: 110px;
	width: 100%;
	
	display: flex;
    flex-wrap: nowrap;
    /*white-space: nowrap;*/
    overflow-x: auto;
    text-align: center;
    justify-content: space-between;
}
.sale-list li {
	flex: 1 0 20%;
}
.sale-list li img {
	border:1px solid dimgrey;
	width: 100%;
	margin:auto;
}
.sale-list >li .title {
	margin-bottom:.5rem;
	margin-top:.5rem;
}
.sale-list >li .price {
	display: block;
	color: orangered;
	font-size: 1.3rem;
	font-weight:bolder;
}
.sale-list >li .price .del {
	display: inline;
	font-size: 1rem;
	color:darkgray;
	text-decoration: line-through;
	font-weight:100;
	margin-left:.3rem;
}
</style>


