<template>
	<div class="swiper_2">
		<div class="swiper-container">
	        <div class="swiper-wrapper">
	            <div class="swiper-slide"><img src="../assets/img/swipper_banner_1.jpg"/></div>
	            <div class="swiper-slide"><img src="../assets/img/swipper_banner_2.jpg"/></div>
	            <div class="swiper-slide"><img src="../assets/img/swipper_banner_3.jpg"/></div>
	            <div class="swiper-slide"><img src="../assets/img/swipper_banner_4.jpg"/></div>
	            <div class="swiper-slide"><img src="../assets/img/swipper_banner_5.jpg"/></div>
	            <div class="swiper-slide"><img src="../assets/img/swipper_banner_6.jpg"/></div>
	            <div class="swiper-slide"><img src="../assets/img/swipper_banner_7.jpg"/></div>
	            <div class="swiper-slide"><img src="../assets/img/swipper_banner_8.jpg"/></div>
	        </div>
	        <!-- Add Pagination -->
	        <div class="swiper-pagination"></div>
	    </div>
	</div>
</template>
<script>
import Swiper from '../../static/swiper.min.js';
export default {
  name: 'swiper-list2',
  mounted(){
	  	var swiper = new Swiper('.swiper_2 .swiper-container', {
		    pagination: '.swiper-pagination',
		    nextButton: '.swiper-button-next',
		    prevButton: '.swiper-button-prev',
		    paginationClickable: true,
		    spaceBetween: 30,
		    centeredSlides: true,
		    autoplay: 2500,
		    autoplayDisableOnInteraction: false
		});
	  }
}
</script>
<style>
@import '../assets/css/swiper.css';
	.swiper_2 {
		padding-bottom:1rem;
		border-bottom:1px solid #e2dddd;
	}
	.swiper_2 .swiper-pagination{
		margin-bottom: 0;
		text-align: right;
	}
</style>


