<template>
	 <div>
	   	<header-search></header-search>
		<div class="wrapper">
			<!--轮播图-->
			<div class="swiper">
				<swiper-list1></swiper-list1>
				<swiper-list2></swiper-list2>
			</div>
			<!--四宫格-->
			<four-box></four-box>
			<!--限时抢购-->
			<limit-show></limit-show>
			<!--品质优选-->
			<quality-box></quality-box>
			
			<!--推荐有奖-->
			<recommend-box></recommend-box>
			
			<!--推荐商家-->
			<div class="shop-box">
				<div class="header">推荐商家</div>
				<div class="content-list">
					<ul>
						<shop-box></shop-box>
						<shop-box></shop-box>
						<shop-box></shop-box>
						<shop-box></shop-box>
						<shop-box></shop-box>
						<shop-box></shop-box>
					</ul>
				</div>
			</div>
		</div>
		<footer-bar></footer-bar>
		<shop-car></shop-car>
	</div> 
</template>
<script>
import FooterBar 		from './FooterBar'
import HeaderSearch 	from './HeaderSearch'
import SwiperList1 		from './SwiperList1'
import SwiperList2 		from './SwiperList2'
import FourBox 			from './FourBox'
import LimitShow 		from './LimitShow'
import QualityBox		from './QualityBox'
import RecommendBox		from './RecommendBox'
import ShopBox 			from './ShopBox'
import ShopCar 			from './ShopCar'

export default {
  name: 'index',
  components: {
  	'footer-bar' 	: FooterBar,
  	'header-search' : HeaderSearch,
  	'swiper-list1' 	: SwiperList1,
  	'swiper-list2' 	: SwiperList2,
  	'four-box'		: FourBox,
  	'limit-show'	: LimitShow,
  	'quality-box'	: QualityBox,
  	'recommend-box'	: RecommendBox,
  	'shop-box'		: ShopBox,
  	'shop-car'		: ShopCar

  }
}

</script>
<style>
body {
	background-color: #f5f5f5 !important;
}
.wrapper {
	margin-bottom: 60px;
}
.shop-box {
	margin-top:1rem;
	background-color: #fff;
	padding-top:1rem;
	padding-bottom: 1rem;
}
.shop-box a {
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}
.shop-box .header {
	color: grey;
	padding-bottom: .3rem;
	border-bottom: 1px solid #f1eeee;
	margin-left: 1rem;
}
@media only screen and (min-width: 310px) and (max-width: 350px) {
	.swiper_1 {
		margin-top:40%;
	}
	.swiper_1 .swiper-container {
		margin-top:13rem;
	}
	.sale-list {
		height: 8rem;
	}
	
}
@media only screen and (min-width: 355px) and (max-width:420px) {
	.swiper_1 {
		margin-top:35%;
	}
	.swiper_1 .swiper-container {
		margin-top:10rem;
	}
	.sale-list {
		height: 10rem;
	}
	.sale-list li {
		margin-left:.5rem;
		margin-right:1.5rem;
	}
	.sale-list li img {
		width: 110%;
	}
}
@media only screen and (min-width: 410px)  {
	.hot {
		width: 4.3rem;
		height: 1.3rem;
	}
	.brand {
		width: 4.3rem;
		height: 1.3rem;
	}

}
a {
	color: black;
}
</style>


