<template>
	<div>
		<commin-header-login></commin-header-login>
		<div class="input-info">
			<div class="phone">
				<span class="auth-code"><a href="###">获取验证码</a></span>
				<input type="tel" placeholder="手机号" class="phone">
			</div>
			<input type="text" placeholder="验证码">
		</div>

		<div class="attention">
			<p>温馨提示：未注册饿了么帐号的手机号，登录时将自动注册，且代表您已同意
				<a href="###">《用户服务协议》</a>
			</p>
			<div class="some">
				<button class="submit">登录</button>
				<h3>关于我们</h3>
			</div>
		</div>
	</div>
</template>
<script>
import CommonHeaderLogin 	from './CommonHeaderLogin'

export default {
	name: 'login',
	components: {
  	'commin-header-login' : CommonHeaderLogin
  	}
}
</script>
<style scoped>
.input-info {
	margin-top:45px;
}
.input-info input{
	width: 100%;
	outline: none;
	border:none;
	padding:1rem;
	border-top:1px solid #d2cfcf;
	font-size: 1.6rem;
}
.input-info >input {
	border-bottom:1px solid #d2cfcf;
}
.phone {
	position: relative;
}
.auth-code {
	position: absolute;
	top:1rem;
	right: .5rem;
	display: inline-block;
	background-color: rgba(0,0,0,0.4);
	padding:0.2rem 0.5rem;
	border-radius: .3rem;
	z-index: 5;
}
.auth-code a {
	color: #fff;
}
.attention {
	margin-top: 1rem;
	padding:0 1rem;
}
.attention p {
	color:grey;
	font-size: .8rem;
}
.attention p a {
	color: skyblue;
}
.some {
	text-align: center;
	margin-top: .5rem;
	padding:0 1rem;
}
.some button {
	width: 100%;
	background-color: #65e265;
	color:#fff;
	border-radius: .3rem;
	outline: none;
	border:none;
	padding:.7rem 0;
}
.some h3 {
	margin-top: 1rem;
	color: grey;
	font-size: .8rem;
}
</style>


