		display: block;
		margin:auto;
		width: 5rem;
		height: 5rem;
		border:2px solid gray;
		border-radius: 50%;
		margin-top: 5rem;
		position: relative;
		transition:1s;
	}

	.noticeBox .close:before {
		content:"";
		display: block;
		width: 4rem;
		height: 2px;
		background-color: gray;
		position: absolute;
		top:2.1rem;
		left:4px;
		transform:rotate(45deg);
	}

	.noticeBox .close:after {
		content:"";
		display: block;
		width: 4rem;
		height: 2px;
		background-color: gray;
		position: absolute;
		top:2.1rem;
		left:4px;
		transform:rotate(-45deg);
	}
	.noticeBox .close:hover {
		border:2px solid #fff;
		background-color: #fff;
	}
	@media only screen and (min-width: 310px) and (max-width: 350px) {

		.two-titles >span {
			margin:1rem 3rem;
		}
		.shop-name h1 {
			font-size: 1.6rem;
		}
	}
</style>


