<template>
	<div>
		<router-link v-bind:to="'./login'">
			<div class="user-box">
				<div class="user-head">
					<img src="../assets/img/defaulthead.jpg" alt="">
				</div>
				<div class="user-login">
					<h2>登录/注册</h2>
					<p><i class="iconfont icon-hongbao"></i>登录后享受更多特权</p>
				</div>
				<div class="arrow">
					<i class="iconfont more icon-more"></i>
				</div>
			</div>
		</router-link>
	</div>
</template>
<script>
export default {
	name: 'user-before'
}
</script>

<style scoped>
.user-box {
	margin-top:45px;
	background-color: #0097ff;
	border-top:1px solid skyblue;
	padding:1.5rem 0 1.5rem 3rem;
	display: flex;
	align-items:center;
	justify-content:space-around;
}
.user-head {
	flex-basis:13%;
	text-align: center;
}
.user-head  img {
	border-radius: 50%;
}
.user-login {
	flex-basis:50%;
}
.arrow {
	flex-basis:20%;
	text-align: center;
}
.arrow i {
	color: #fff;
	font-size: 3rem;
}
.user-login h2 {
	font-size: 2.5rem;
	font-weight: 400;
	color: #fff;
}
.user-login p {
	font-size: 1.3rem;
	color: #fff;
}
.user-login p i {
	font-size: 1.5rem;
	margin-right:.6rem;
}
</style>


