<template>
	<div>
		<footer id="footer">
			<ul>
				<li v-for="(item, index) in fooList"
				 v-bind:class="[item.style, {active:$route.path === item.path}]">
					<router-link v-bind:to="item.path">
						{{ item.text}}
					</router-link>
				</li>
			</ul>
		</footer>
	</div>
</template>
<script>
export default {
	name: 'footerbar',
	data() {
		return {
			fooList:[{
				path:'/',
				text:'外卖',
				style: 'iconfont icon-changyonglogo40'
			},{
				path:'/discover',
				text:'发现',
				style: 'iconfont icon-faxian'
			},{
				path:'/order',
				text:'订单',
				style: 'iconfont icon-dingdan'
			},{
				path:'/my',
				text:'我的',
				style: 'iconfont icon-iconfontwohover'
			},]
		}
	}
}
</script>

<style scoped>
#footer {
	height: 50px;
	max-width: 720px;
	width: 100%;
	background-color: #f7f7f7;
	position:fixed;
	bottom:0;
}
#footer ul {
	border-top:1px solid #d9d9d9;
    display: flex;
    font-size: 1rem;
}
#footer ul li {
	flex:1;
	text-align: center;
}
#footer ul li a {
	margin-top:0px;
	display: block;
	color:#333;
	font-size:1.2rem;
	-webkit-tap-highlight-color:rgba(0,0,0,0);
	-webkit-user-select: none;
}

#footer ul .active a {
    color: #0095fc;
}
</style>


